import React, { useEffect, useState } from 'react';
import { Form, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import Message from '../components/Message';
import Loader from '../components/Loader';
import { getUserDetails, updateUser } from '../actions/userActions';
import FormContainer from '../components/FormContainer';
import { USER_UPDATE_RESET } from '../constants/userConstants';

const UserEditScreen = ({ match, history }) => {
	const userId = match.params.id;

	const [name, setName] = useState('');
	const [email, setEmail] = useState('');
	const [isAdmin, setIsAdmin] = useState('');

	const dispatch = useDispatch();

	//Get user Details.
	const userDetails = useSelector((state) => state.userDetails);
	const { loading, error, user } = userDetails;

	const userUpdate = useSelector((state) => state.userUpdate);
	const {
		loading: loadingOnUpdate,
		error: errorOnUpdate,
		success: successOnUpdate,
	} = userUpdate;

	useEffect(() => {
		if (successOnUpdate) {
			dispatch({ type: USER_UPDATE_RESET });
			history.push('/admin/userslist');
		} else {
			if (!user || user._id !== userId) {
				dispatch(getUserDetails(userId));
			} else {
				setName(user.name);
				setEmail(user.email);
				setIsAdmin(user.isAdmin);
			}
		}
	}, [user, userId, dispatch, successOnUpdate, history]);

	//Handle what happens when a admin click to submit the form details to update user
	const submitHandler = (e) => {
		e.preventDefault();
		dispatch(updateUser({ _id: userId, name, email, isAdmin }));
	};
	return (
		<>
			<Link to='/admin/userslist' className='btn btn-light my-3'>
				GO Back
			</Link>

			<FormContainer>
				<h1>Edit User</h1>
				{loadingOnUpdate && !loading && <Loader />}
				{errorOnUpdate && <Message variant='danger'>{error}</Message>}
				{loading ? (
					<Loader />
				) : error ? (
					<Message variant='danger'>{error}</Message>
				) : (
					<Form onSubmit={submitHandler}>
						<Form.Group controlId='name'>
							<Form.Label>Enter Name</Form.Label>
							<Form.Control
								type='text'
								placeholder='Enter Your Name'
								value={name}
								onChange={(e) => setName(e.target.value)}></Form.Control>
						</Form.Group>
						<Form.Group controlId='email'>
							<Form.Label>Email Address</Form.Label>
							<Form.Control
								type='email'
								placeholder='Enter Your Email'
								value={email}
								onChange={(e) => setEmail(e.target.value)}></Form.Control>
						</Form.Group>
						<Form.Group controlId='isAdmin'>
							<Form.Label>Is Admin</Form.Label>
							<Form.Check
								type='checkbox'
								checked={isAdmin}
								onChange={(e) => setIsAdmin(e.target.checked)}></Form.Check>
						</Form.Group>

						<Button type='submit' variant='primary'>
							Update
						</Button>
					</Form>
				)}
			</FormContainer>
		</>
		//Form container is a component, and inside details are the children
	);
};

export default UserEditScreen;
